.player-data {
    color: white;
    width: 75%;
    max-width: 1080px;
    border-right: 1px solid rgba(255, 255, 255, 0.5);
    padding-right: 100px;
    margin-top: 50px;
    min-height: 650px;
}

.player-data-container {
    color: white;
    display: flex;
    max-width: 1440px;
    width: 75%;
    margin: 0 auto;
    justify-content: space-between;
}

.player-data-top {
    width: 100%;
    display: flex;
    justify-content:space-between;
    align-items: end;
    margin-bottom: 25px;
}

.player-data-top .player-name .first {
    font-family: 'Londrina Outline', cursive;
    font-size: 48px;
    margin-bottom: -20px;
}

.player-data-top .player-name .last {
    font-family: 'Londrina Solid', cursive;
    font-size: 84px;
}

.player-data-top .player-image img {
    width: 260px;
    position: relative;
}

.player-data-bottom {
    display: flex;
    justify-content:space-between;
    font-family: 'Roboto', sans-serif;
    margin-bottom: 25px;
}

.stat-label {
    font-weight: 100;
    font-size: 18px;
    margin-bottom: -10px;
    text-transform: uppercase;
}

.stat {
    font-weight: 400;
    font-size: 36px;
}

.stats-col {
    display: flex;
    flex-direction: column;
    height: 300px;
    justify-content: space-between;
    width: 25%;
}

.team-name {
    font-size: 36px;
    margin-bottom: 25px;
}

.favourite-heart {
    position: absolute;
    font-size: 24px;
    cursor: pointer;
}

.favourite-heart.fill {
    color: red;
}

@media only screen and (max-width: 950px) {
    .player-data-container {
        flex-direction: column;
        align-items: center;
    }

    .player-data {
        padding-right: 0;
        width: 100%;
        border-right: none;
        border-bottom: 1px solid rgba(255, 255, 255, 0.5);
        padding-bottom: 50px;
    }

}

@media only screen and (max-width: 650px) {

    .player-data-top {
        flex-direction: column;
        align-items: center;
    }

    .stats-col {
        width: 50%;
        align-items: center;
        margin-bottom: 25px;
    }

    .player-data-bottom {
        flex-wrap: wrap;
    }

}